css .plinko-dropper-container {
    position: relative;
    height: 105vh; /* Try and prevent oversized components if zooming */
    width: 85vw;
    max-height: 1300px;
    max-width: 1200px;
    min-height: 800px;
    min-width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.dropper-and-labels-container {
    position: relative;
    width: 100%;
    height: 785px;
}

.dropper-canvas {
    background-color: #ffffff;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.bucket-labels {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    bottom: 0px; /* Use pixel value for fixed position - need to adapt */
    width: 100%;
    line-height: 30px;
    left: 47.9%;
}

.bucket-label {
    text-align: center;
    color: #0B3239;
    font-weight: bold;
    font-size: 1.5vh; 
}

.control-padding {
    padding-top: 2vh; 
}

.controls {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    margin-top: 2vh; 
}

.controls button,
.controls input,
.controls select {
    margin: 1vh 0; 
}

@media (max-width: 600px) {
    .plinko-dropper-container {
        height: 75vh; 
        width: 90vw;
    }

    .bucket-labels {
        bottom: 100px;
    }

    .bucket-label {
        font-size: 1.2vh; 
    }

    .control-padding {
        padding-top: 1vh; 
    }

    .controls {
        margin-top: 1vh; 
    }

    .controls button,
    .controls input,
    .controls select {
        margin: 0.5vh 0; 
    }
}

.slider-container {
    display: flex;
    align-items: center;
}

.slider-value {
    margin-left: 10px;
}


.button {
  padding: 0.6em 2em;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}



.button {
    position: relative;
    width: 25%;
    height: 80px; /* Adjusted height to make it chunkier */
    background-color: #00CCD0;
    border-radius: 10px;
    overflow: hidden;
    display: inline-block;
    text-align: center;
    color: white;
    font-size: 20px; /* Adjust font-size as needed */
    font-weight: bold;
    cursor: pointer;
    border: 2px solid #0B3239; /* Added border */
  }
  
  .button.alternate {
    background-color: #8BA6FA; /* Background color for alternate button */
  }
  
  
.color-guide {
    display: flex;
    width: 100%;
  }
  
  .color-guide-item {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: inherit;
    padding: 10px;
    font-weight: bold;
    color: #0B3239;
    text-align: center;
  }
  
  /* Adjustments for iPhone landscape mode */
@media (max-width: 900px) and (orientation: landscape) {
    .plinko-dropper-container {
        height: 80vh; /* Adjust height for landscape mode */
        width: 95vw; /* Adjust width for landscape mode */
        max-height: 375px; /* Max height to fit landscape */
        max-width: 812px; /* Max width to fit landscape */
        min-height: 200px; /* Min height for smaller devices */
        min-width: 300px;
    }

    .dropper-and-labels-container {
        height: 100%; /* Full height of the container */
    }

    .bucket-labels {
        bottom: 50px; /* Adjust position of labels */
    }

    .bucket-label {
        font-size: 1.0vh; /* Smaller font for labels */
    }

    .control-padding {
        padding-top: 1vh; /* Adjust padding */
    }

    .controls {
        margin-top: 1vh; /* Adjust margin */
        flex-direction: column; /* Stack controls vertically */
        align-items: flex-start; /* Align controls to start */
    }

    .controls button,
    .controls input,
    .controls select {
        margin: 0.5vh 0; /* Adjust margins */
        width: 100%; /* Full width for controls */
    }
}
