/* Add to your CSS file, e.g., App.css or styles.css */

@font-face {
  font-family: 'MyCustomFont';
  src: url('fonts/PixelifySans-Regular.ttf') format('truetype'); /* Fixed format typo */
  font-weight: normal;
  font-style: normal;
}

* {
  font-family: sans-serif; /* Add fallback font */
}

.app-container {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #FFCCD9;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
